body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#16202b}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.sign-in{background-repeat:no-repeat;background-position:center center;background-size:cover;background-color:#F77F00}.sign-in_content{display:flex;flex-direction:column;align-items:center;min-height:100vh}.sign-in_content_logo{padding:40px 20px;width:500px}.sign-in_content_logo img{width:100%}.sign-in_content_tabs{background-color:#fff}.sign-in_content_tabs .ant-tabs{width:90vw}@media (min-width: 992px){.sign-in_content_tabs .ant-tabs{width:auto}}.sign-in_content_tabs .ant-tabs-nav-container{height:60px !important}.sign-in_content_tabs .ant-tabs-nav{width:100%}.sign-in_content_tabs .ant-tabs-tab{width:50px;height:60px !important;text-align:center;justify-content:center;font-size:1.1875em;border-top:4px solid gray !important;border-radius:0 !important;margin:0 !important}@media (min-width: 992px){.sign-in_content_tabs .ant-tabs-tab{width:220px;font-size:1.4375em}}.sign-in_content_tabs .ant-tabs-tab:hover{color:#000}.sign-in_content_tabs .ant-tabs-tab:before{content:none}.sign-in_content_tabs .ant-tabs-tab span{height:100%;display:flex;align-items:center}.sign-in_content_tabs .ant-tabs-tab-active{color:#054743 !important;border-top:4px solid #054743 !important;border-radius:0 !important;border-right:0 !important;border-left:0 !important}.sign-in_content_tabs .ant-tabs-content{padding:0 16px 16px 16px;text-align:center}
